@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: black!important; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* #navitems{
    
} */
.bg-nav{
    background-color: rgba(22, 4, 4, 0.89)!important;
    height: 100px;
}

body{
    font-family: Roboto,Helvetica,sans-serif;
    color: #000;
    background-color: #c1c1c1;
    font-size: 14px;
    line-height: 1.5;
}
.red{
    color: #BB1E1E;

}
*, :after, :before {
    box-sizing: inherit;
}
.resume-build{
    width: 920px;
    padding: 36px 65px 60px;
    margin: 80px auto;
    background-color: #fff;
    box-shadow: 2px 2px 1px #606060;
}
.resume-section{
    margin-top: 1.5em;
} 
/* _______HEADER _______ */

.resume-header{
    display: flex;
}
/* Container inside resume section */
.resume-section-text{
    flex: 1 1;
    padding: 0 15px;
}

.resume-header-name{
    display: block;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 30px;
    line-height: 1;
    margin-bottom: 5px;
    color: #BB1E1E;
}
.resume-header-subname{
    display: block;
    text-transform: none;
    font-size: 24px;
    font-weight: 300;
    margin-bottom: 5px;
    color: #777;
}
.resume-header-info{}

.hcltech{
    border-left: 2px solid #736b6b;
    margin-left: 5px;
    padding-left: 5px;
}
/* container inside resume section */
.resume-section-meta{
    font-size: 14px;
    color: #BB1E1E;
    white-space: nowrap;
}
.resume-section-meta-right{
    float: right;
    border-left: 1px solid #e0e0e0;
    padding-left: 15px;
    text-align: left;
}
.resume-section-meta-row a{
    text-decoration: none;
}
.resume-section-meta-row a:hover{
    color: #BB1E1E;
    text-decoration: underline;
}


/* Resume sections */
.resume-section-title{
    color: #BB1E1E;
    font-size: 24px;
    font-weight: 300;
    padding-left: 1.5%;
    margin-bottom: 15px;
    page-break-after: avoid;
}
.resume-section-content{
    /* padding-left: 1.5%; */
}

.project-info{
    padding-left: 1.5%;
}
.project-info h4{
    font-weight: 400;
    font-size: 18px;
    color: #333;
    line-height: 1.3;
}
.project-info a{
    text-decoration: none;
    color: #BB1E1E;
}
.education-content{}
.education-content .institute-name{
    float: left;
    width: 23%;
    text-align: center;
    padding-right: 15px;
    color: #777;
    font-size: 13px;
}
.education-content .institute{
    background-color: #eee;
    padding: 3px 7px;
    margin-bottom: 5px;
    border-radius: 2px;
}
.education-content .studyname .course-title{
    font-weight: 400;
    font-size: 18px;
    color: #333;
    line-height: 1.3;
}
.certifications ul{}
.certifications ul li{

}
.resume-section ul li a{
    text-decoration: none;
    color: rgb(87, 41, 41);
}

.download-cv{
    text-align: center;
    margin-bottom: 20px;
    
}
.download-cv a{
    text-decoration: none;
    border: 2px solid white;
    background-color:rgb(2, 2, 29) ;
    padding: 10px;
    color: white;
    border-radius: 5px;
    
}
.cv-timeline-row {
    page-break-inside: avoid;
    overflow: hidden;
    display:flex;

}

.cv-timeline-side {
    float: left;
    width: 17%;
    text-align: right;
    padding-right: 15px;
    color: #777;
    font-size: 13px;
}

.cv-timeline-company {
    text-transform: uppercase;
}

a {
    text-decoration: none;
}

.red, a {
    color: #BB1E1E;
}
a {
    background: 0 0;
}
.cv-timeline-body {
    float: left;
    width: 70%;

}
.cv-timeline-position {
    font-weight: 400;
    font-size: 18px;
    color: #333;
    line-height: 1.3;
}

.cv-timeline-body p, .cv-timeline-body ul {
    margin-top: 5px;
    margin-bottom: 5px;
}
.tou-node {
    all: unset;
    will-change: opacity;
    transition-property: opacity;
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
    opacity: 1;
}
.cv-timeline-body p, .cv-timeline-body ul {
    margin-top: 5px;
    margin-bottom: 5px;
}
.cv-section-content ol, .cv-section-content ul:not(.tech-tags) {
    padding-left: 20px;
}
ul {
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    -webkit-padding-start: 40px;
            padding-inline-start: 40px;
}
li {
    display: list-item;
    text-align: -webkit-match-parent;
}
*, :after, :before {
    box-sizing: inherit;
}

@media screen and (min-width: 768px){
.cv-timeline-company {
    background-color: #eee;
    padding: 3px 7px;
    margin-bottom: 5px;
    border-radius: 2px;
}

}
